@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100%;
}

.header {
  background: black;
  animation: fadeIn 4s;
  border: 0px;
  display: flex;
  color: white;
  align-items: center;
  font-size: 2rem;
  margin:0;
  clear:none;
  position: relative;
  padding-top: 1rem;
  height: auto;
  opacity: 0.8;
}

.dropdown {
  position: relative;
  display: flex;
  white-space: nowrap;
  padding-left: 10rem;
  justify-content: flex-end;
}
 

.header--icons {
  padding-left: 2rem;
  display: flex;
  position: relative;
  justify-content: flex-start;
}

.header--name{
  color: #fff;
  animation: fadeIn 1s;
  margin-left: 50px;
  transition: transform 0.3s ease-in-out;
  font-family: "Poppins", sans serif;
  white-space: nowrap;
}

.header--name:hover {
  transform: scale(1.5) rotate(10deg);
}


.main-section {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: .3;
  width: 100vw;
  height: 100vh;
  display: inline;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 2s ease-in-out;
}

.bg-image {
  /* background-image: url('../public/imgs/bg1.png'); */
  background-image: var(--bg-image);
}

.main--simon{
  animation: fadeIn 4s;
  padding-top: 200px;
  align-items: center;
  margin: auto;
  width: 200px;
  height: 200px;
  font-family: PT Sans, sans serif;
  color: #fff;


}

.simon--img{
  border-radius:45%;
  position: relative;
  cursor: pointer;
  align-items: center;
  border: 3px solid rgba(0, 0, 0, 0.617);
  overflow: hidden;
  width: 200px;
  transition: transform 0.5s ease-in-out;
}

.simon--img:hover {
  transform:scale(1.5);
  /* transform: rotateY(360deg); */
}

.simon--name{
  animation: fadeIn 4s;
  align-items: center;
  margin: auto;
  display: flexbox;
  font-family: "Poppins", sans-serif;
  color: rgba(0, 0, 0, 0.8);
  font-size:5em;
  overflow: hidden;
  padding-top: 200px;
  top: 0;
  border: 0px;
  clear:none;
  /* text-shadow: 1px  1px 50px gold; */
  transition: transform 0.3s ease-in-out;}

.icon-box {
  position: relative;
  align-items: center;
  padding-top: 200px;
  justify-content: space-between;
  display: flexbox;
  top: 500%;
}

/* .simon--name:hover {
  transform: scale(1.5) rotate(-10deg);
} */

.icon {
  margin: 10px;
  width: 3rem;
  /* width: 40px; */
  transition: transform 0.2s ease, box-shadow 0.3s ease-in-out;
}

.icon-text {
  position: absolute;
  bottom: 0;
  /* left: 37px;  */
  top: calc(100% + 0.01rem);
  color: black;
  font-family: "Poppins", sans serif;
  text-align: center; 
  /* padding-left: px; */
  display: absolute;
  font-size: 20px;
  opacity: 0.0;
  transition: opacity 0.3s ease;
}



.icon:hover {
  transform: scale(1.8) ;
  /* box-shadow:  0 0 15px 10px rgba(0, 150, 255, 0.7); */
}

.icon:hover + .icon-text {
  opacity: 1.0;
}


.dropdown--menu {
  position: absolute;
  left: 10rem;
  top: calc(100% + 0.5rem);
  color: black;
  background-color: rgba(16, 16, 16, 0.359);
  white-space: nowrap;
  font-family: PT Sans, sans serif;
  font-size: 15px;
  padding: 2rem;
  /* padding-left: 25rem; */
  border-radius: 0.1rem;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.4);
  opacity: 0;
  /* pointer-events: none; */
  transform: translate(-10px);
  transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;
}

.pdf_document{
  z-index: -5;
}

.dropdown--menu:hover {
  color: orange;
}

.dropdown > .link:focus + .dropdown--menu {
  opacity: 1;
  transform: translateY(0);
  /* pointer-events: auto; */

}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-10px); }
  20%, 40%, 60%, 80% { transform: translateX(10px); }
}


.button-image {
  margin-right: 8px; /* Adjust the margin as needed */
  width: 20px; /* Adjust the width as needed */
  height: 20px; /* Adjust the height as needed */
}

.button-link {
  display: flex;
  align-items: center;
  border: none; /* Add your button styling */
  color: white;
  padding-bottom: 10px;
  /* background-color: #f0f0f0; Add your button styling */
  /* padding: 10px; Add your button styling */
  cursor: pointer; /* Add your button styling */
  text-decoration: underline;
}


.dropdown-link {
  color: white;
}

.dropdown-link:hover {
  color: black;
}

.dropdown-link:active {
  color: #00a8ff;
}


.documents_box {
  width: 40rem;
  height: 10rem; 
  display: flexbox;
  background-color: black;
  /* border: 2px solid white; */
  opacity: 0.5;
  margin: auto;
  margin-top: 1.5rem;
}

.document_box {
  position: absolute;
  display: absolute;
}

.documents_box_img {
  width: 100px;
  height: 100px;
}

.documents-link-txt {
  position: absolute;
  display: absolute;
  color: white;
  height: 100px;
  font-size: 50rem;
  color: white;
}